import React, { useEffect } from 'react';
import { generatePath } from 'react-router-dom';

import { TokenFormatter } from 'shared/helpers/formatting';
import { useRoketoContext } from 'app/roketo-context';
import { CreateStreamForm, CreateStreamFormValues } from 'features/create-stream/CreateStreamForm';
import { useAccount } from 'features/roketo-resource';

const redirectUrl = generatePath('streams');
const returnPath = `${window.location.origin}/#/${redirectUrl}`;

export function SendPage() {
  const { roketo, tokens, near } = useRoketoContext();
  const account = useAccount();
  const createStreamClick = async (values: CreateStreamFormValues) => {
    const {
      receiver,
      autoStart,
      comment,
      deposit,
      speed,
      token
    } = values;

    console.log('values', values)

    const formatter = TokenFormatter(tokens.get(token).metadata.decimals);

    await roketo.api.createStream(
      {
        deposit: formatter.toInt(deposit),
        description: comment,
        receiverId: receiver,
        token,
        speed: String(speed),
        isAutoStartEnabled: autoStart,
      },
      {
        callbackUrl: returnPath,
      },
    );
  };

  useEffect(() => {
    (async () => {
      try {
        if (!sessionStorage.testAPI) {
          return;
        }

        if (!account.data) {
          return;
        }

        const accountId = account.data.account_id;

        const keyPair = await near.config.keyStore.getKey(near.config.networkId, accountId);

        const LOGIN_MESSAGE = process.env.LOGIN_MESSAGE || 'ROKETO-LOGIN';

        const message = `${LOGIN_MESSAGE}-${Date.now()}`;

        const { signature: Uint8Signature } = keyPair.sign(new TextEncoder().encode(message)); // TODO: Polyfill TextEncoder?

        const signature = Array.from(Uint8Signature);

        const authResponse = await fetch('https://roketo-test-api.herokuapp.com/auth/login', {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json',
          },
          body: JSON.stringify({
            accountId,
            message,
            signature,
          }),
        });

        if (!authResponse.ok) {
          console.log('Not OK :(');

          return;
        }

        const { access_token: JWTToken } = await authResponse.json();

        console.log(`Authenticated successfully as ${accountId}!`);

        const dataResponse = await fetch('https://roketo-test-api.herokuapp.com', {
          headers: {
            Authorization: `Bearer ${JWTToken}`,
          },
        });

        const data = await dataResponse.text();

        console.log(`Authorized API response: "${data}"`);
      } catch(e) {
        console.log('Login unsuccessful :(');
      }
    })();
  }, [near, account.data]);

  return (
    <div className="container m-auto px-5 py-12">
      <div className="text-center">
        <h1 className="text-5xl mb-4 text-center">Create a stream</h1>
        <p className="text-gray">Stream your tokens to the receiver directly</p>
      </div>
      <CreateStreamForm onSubmit={createStreamClick} />
    </div>
  );
}
